/**
 * Systme de tracking pour Yummeal
 * 
 * Ce module gre l'envoi d'vnements de tracking vers:
 * - Notre API Deno
 * - Facebook Pixel
 */

import { trackFacebookEvent } from './facebookPixel';

// Configuration
const API_URL = 'https://yummeal-server.deno.dev/tracking';

// Types
type Platform = 'apple' | 'google';

/**
 * @deprecated Utiliser les nouveaux types et fonctions
 */
interface TrackingEvent {
  platform: Platform;
  location: string;
  ip?: string;
  userAgent?: string;
}

/**
 * Envoie un vnement  notre API Deno
 * @param eventName Nom de l'vnement
 * @param data Donnes associes  l'vnement
 * @returns Promise<boolean> Succs de l'envoi
 */
const sendToDeno = async (eventName: string, data: Record<string, unknown>): Promise<boolean> => {
  // Prparer les donnes avec mtadonnes standard
  const eventData = {
    name: eventName,
    properties: {
      ...data,
      timestamp: new Date().toISOString(),
      source: 'yummeal_website'
    }
  };

  try {
    const response = await fetch(API_URL, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify(eventData)
    });
    
    if (!response.ok) {
      throw new Error(`API responded with status ${response.status}`);
    }
    
    return true;
  } catch (error) {
    console.error('[TRACKING] Erreur d\'envoi  l\'API:', error);
    // Sauvegarder l'vnement pour ressayer plus tard
    saveFailedEvent(eventData);
    return false;
  }
};

/**
 * Envoie un vnement  toutes les destinations configures
 * @param eventName Nom de l'vnement
 * @param data Donnes associes  l'vnement
 */
export const sendEvent = async (eventName: string, data: Record<string, unknown> = {}): Promise<void> => {
  // Envoi parallle aux diffrentes destinations
  await Promise.all([
    sendToDeno(eventName, data),
    Promise.resolve(trackFacebookEvent(eventName, data))
  ]);
};

/**
 * Enregistre une vue de page
 */
export const trackPageView = (): void => {
  // Collecter les donnes de la page
  const pageData = {
    page_url: window.location.href,
    page_title: document.title,
    referrer: document.referrer || 'direct',
    screen_width: window.innerWidth,
    screen_height: window.innerHeight,
    user_agent: navigator.userAgent,
    language: navigator.language,
    timestamp: new Date().toISOString()
  };

  // Envoi  l'API Deno
  sendToDeno('page_view', pageData);

  // Envoi  Facebook Pixel
  trackFacebookEvent('PageView', {
    page_path: window.location.pathname
  });
  
  console.log('[TRACKING] Page view:', window.location.pathname);
};

// ===== Fonctions utilitaires pour la collecte de donnes =====

/**
 * Liste des paramtres UTM  collecter
 */
const UTM_PARAMETERS = [
  'utm_source',
  'utm_medium',
  'utm_campaign',
  'utm_content',
  'utm_term'
];

/**
 * Collecte les identifiants Facebook (fbp, fbc) depuis les cookies et l'URL
 * @returns Objet contenant les identifiants Facebook disponibles
 */
const getFacebookIds = (): { fbp?: string; fbc?: string } => {
  const result: { fbp?: string; fbc?: string } = {};
  
  // Scurit pour les environnements non-navigateur
  if (typeof document === 'undefined') return result;
  
  try {
    // Rcuprer le Facebook Browser ID (fbp) depuis les cookies
    const cookies = document.cookie.split('; ');
    const fbpCookie = cookies.find(cookie => cookie.startsWith('_fbp='));
    if (fbpCookie) {
      result.fbp = fbpCookie.split('=')[1];
    }
    
    // Rcuprer le Facebook Click ID (fbc) depuis les cookies ou l'URL
    const fbcCookie = cookies.find(cookie => cookie.startsWith('_fbc='));
    if (fbcCookie) {
      result.fbc = fbcCookie.split('=')[1];
    } else if (typeof window !== 'undefined') {
      // Chercher fbclid dans l'URL
      const urlParams = new URLSearchParams(window.location.search);
      const fbclid = urlParams.get('fbclid');
      if (fbclid) {
        // Format standard pour fbc
        result.fbc = `fb.1.${Date.now()}.${fbclid}`;
      }
    }
  } catch (error) {
    console.error('[TRACKING] Erreur lors de la collecte des identifiants Facebook:', error);
  }
  
  return result;
};

/**
 * Rcupre les paramtres UTM depuis l'URL
 * @returns Objet contenant les paramtres UTM prsents dans l'URL
 */
const getUtmParameters = (): Record<string, string> => {
  const utmParams: Record<string, string> = {};
  
  // Scurit pour les environnements non-navigateur
  if (typeof window === 'undefined') return utmParams;
  
  try {
    const urlParams = new URLSearchParams(window.location.search);
    
    UTM_PARAMETERS.forEach(param => {
      const value = urlParams.get(param);
      if (value) {
        utmParams[param] = value;
      }
    });
  } catch (error) {
    console.error('[TRACKING] Erreur lors de la collecte des paramtres UTM:', error);
  }
  
  return utmParams;
};

/**
 * Dtecte le type d'appareil  partir du user-agent
 * @returns 'mobile' ou 'desktop'
 */
const getDeviceType = (): 'mobile' | 'desktop' => {
  if (typeof navigator === 'undefined') return 'desktop';
  return /Mobile|Android|iPhone|iPad|iPod/i.test(navigator.userAgent) ? 'mobile' : 'desktop';
};

/**
 * Enregistre un vnement de dbut de tlchargement
 * @param platform Plateforme cible (ios/android)
 * @param buttonLocation Emplacement du bouton sur la page
 */
export const trackDownloadStart = (platform: string, buttonLocation?: string): void => {
  const data = {
    platform,
    button_location: buttonLocation || window.location.pathname,
    device_type: getDeviceType(),
    timestamp: new Date().toISOString()
  };
  
  // Envoi de l'vnement
  sendEvent('start_download', data);
  
  console.log('[TRACKING] Dbut de tlchargement:', platform);
};

/**
 * Enregistre un vnement de clic sur un bouton de tlchargement
 * Format conforme aux exigences de tracking
 * 
 * @param platform Plateforme cible (ios/android)
 * @param buttonLocation Emplacement du bouton sur la page
 */
export const trackDownloadClick = (platform: Platform | string, buttonLocation?: string): void => {
  console.group('%c[TRACKING] vnement download_click', 'color: #4CAF50; font-weight: bold; font-size: 12px');
  console.log('%cPlateforme:', 'font-weight: bold', platform);
  console.log('%cEmplacement:', 'font-weight: bold', buttonLocation || window.location.pathname);
  
  // Donnes de base requises
  const data: Record<string, unknown> = {
    // Champs obligatoires
    platform,
    button_location: buttonLocation || window.location.pathname,
    
    // Donnes de navigation
    url: typeof window !== 'undefined' ? window.location.href : '',
    referrer: typeof document !== 'undefined' ? (document.referrer || 'direct') : 'direct',
    user_agent: typeof navigator !== 'undefined' ? navigator.userAgent : '',
    
    // Mtadonnes
    timestamp: new Date().toISOString(),
    device_type: getDeviceType()
  };
  
  try {
    // Enrichir avec les paramtres UTM
    const utmParams = getUtmParameters();
    Object.assign(data, utmParams);
    console.log('%cParamtres UTM:', 'font-weight: bold', utmParams);
    
    // Enrichir avec les identifiants Facebook
    const fbIds = getFacebookIds();
    if (fbIds.fbp) data.fbp = fbIds.fbp;
    if (fbIds.fbc) data.fbc = fbIds.fbc;
    console.log('%cIdentifiants Facebook:', 'font-weight: bold', fbIds);
    
    // Envoi  l'API Deno
    console.log('%cEnvoi  l\'API Deno', 'font-weight: bold');
    sendToDeno('download_click', data)
      .then(success => {
        console.log(`%cEnvoi  l'API Deno: ${success ? ' Succs' : ' chec'}`, 'font-weight: bold');
      })
      .catch(error => {
        console.error('Erreur lors de l\'envoi  l\'API:', error);
      });
    
    // Envoi  Facebook Pixel avec donnes adaptes
    const fbData = {
      content_name: platform,
      content_category: 'app_download',
      button_location: data.button_location as string,
      ...utmParams
    };
    
    console.log('%cEnvoi  Facebook Pixel (Lead):', 'font-weight: bold', fbData);
    trackFacebookEvent('Lead', fbData);
    
    // Envoi galement comme vnement personnalis pour plus de visibilit
    console.log('%cEnvoi  Facebook Pixel (download_click):', 'font-weight: bold', fbData);
    trackFacebookEvent('download_click', fbData);
    
    // Afficher les donnes compltes
    console.log('%cDonnes compltes de l\'event:', 'font-weight: bold');
    console.table(data);
    
    console.log('%c Tracking de tlchargement termin', 'color: #4CAF50; font-weight: bold');
  } catch (error) {
    console.error('%c Erreur lors du tracking de tlchargement:', 'color: #F44336; font-weight: bold', error);
  } finally {
    console.groupEnd();
  }
};

/**
 * @deprecated Utiliser trackDownloadClick  la place
 */
export const trackDownload = (event: TrackingEvent): void => {
  console.warn('[TRACKING] La fonction trackDownload est dprcie. Utilisez trackDownloadClick  la place.');
  
  // Conversion vers le nouveau format
  trackDownloadClick(event.platform, event.location);
};

/**
 * Sauvegarde un vnement qui a chou dans le localStorage pour ressayer plus tard
 * @param event vnement qui a chou
 */
export function saveFailedEvent(event: unknown): void {
  if (typeof localStorage === 'undefined') return;
  
  try {
    // Rcuprer les vnements existants
    const failedEvents = JSON.parse(localStorage.getItem('failed_events') || '[]');
    
    // Ajouter le nouvel vnement avec horodatage
    const eventWithTimestamp = {
      event,
      timestamp: new Date().toISOString(),
      retryCount: 0
    };
    
    failedEvents.push(eventWithTimestamp);
    
    // Limiter le nombre d'vnements stocks (max 50)
    const trimmedEvents = failedEvents.slice(-50);
    
    // Sauvegarder
    localStorage.setItem('failed_events', JSON.stringify(trimmedEvents));
    console.log('[TRACKING] vnement sauvegard pour ressai ultrieur');
  } catch (error) {
    console.error('[TRACKING] Erreur de sauvegarde locale:', error);
  }
}

/**
 * Tente de renvoyer les vnements qui ont chou prcdemment
 * @returns Nombre d'vnements ressays
 */
export async function retryFailedEvents(): Promise<number> {
  if (typeof localStorage === 'undefined') return 0;
  
  try {
    // Rcuprer les vnements chous
    const failedEventsString = localStorage.getItem('failed_events');
    if (!failedEventsString) return 0;
    
    const failedEvents = JSON.parse(failedEventsString);
    if (!failedEvents.length) return 0;
    
    console.log(`[TRACKING] Tentative de renvoi de ${failedEvents.length} vnements chous`);
    
    // Filtrer les vnements  ressayer (max 10  la fois)
    const eventsToRetry = failedEvents.slice(0, 10);
    const remainingEvents = failedEvents.slice(10);
    
    // Dfinir le type pour les vnements chous
    interface FailedEventItem {
      event: {
        name?: string;
        properties?: Record<string, unknown>;
      };
      timestamp: string;
      retryCount: number;
    }
    
    // Ressayer chaque vnement
    const results = await Promise.allSettled(
      eventsToRetry.map(async (item: FailedEventItem) => {
        if (item.event?.name && item.event?.properties) {
          return fetch(API_URL, {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify(item.event)
          });
        }
        return Promise.reject('Format d\'vnement invalide');
      })
    );
    
    // Compter les succs et checs
    const succeeded = results.filter(r => r.status === 'fulfilled').length;
    
    // Mettre  jour le localStorage avec les vnements restants
    localStorage.setItem('failed_events', JSON.stringify(remainingEvents));
    
    console.log(`[TRACKING] ${succeeded}/${eventsToRetry.length} vnements renvoys avec succs`);
    return succeeded;
  } catch (error) {
    console.error('[TRACKING] Erreur lors du ressai des vnements:', error);
    return 0;
  }
}
