/**
 * Type dfinitions pour le SDK Facebook Pixel
 */
declare global {
  interface Window {
    fbq: {
      (command: string, pixelId: string): void;
      (command: string, eventName: string, params?: Record<string, unknown>): void;
      callMethod?: (...args: unknown[]) => void;
      queue: unknown[];
      push: typeof Array.prototype.push;
      loaded: boolean;
      version: string;
    };
    _fbq?: Window['fbq'];
  }
}

/**
 * ID du pixel Facebook
 */
const FB_PIXEL_ID = '860752789503495';

/**
 * Vrifie si le pixel Facebook est dj charg
 */
const isPixelLoaded = (): boolean => {
  return typeof window !== 'undefined' && typeof window.fbq === 'function';
};

/**
 * Initialise le SDK Facebook Pixel
 */
export const initFacebookPixel = (): void => {
  // Scurit pour les environnements non-navigateur
  if (typeof window === 'undefined') {
    return;
  }

  // viter la double initialisation
  if (isPixelLoaded()) {
    console.log('[Facebook Pixel] Dj initialis');
    return;
  }

  // Cration de la fonction fbq
  const fbq = function(command: string, ...args: unknown[]): void {
    if (!fbq.queue) fbq.queue = [] as unknown[][];
    fbq.queue.push([command, ...args]);
  };

  // Initialisation des proprits
  fbq.queue = [] as unknown[][];
  fbq.loaded = true;
  fbq.version = '2.0';
  fbq.push = Array.prototype.push;

  // Assignation  window
  window.fbq = fbq;
  window._fbq = fbq;

  // Cration du script
  const script = document.createElement('script');
  script.async = true;
  script.src = 'https://connect.facebook.net/en_US/fbevents.js';

  // Insertion du script dans le document
  const firstScript = document.getElementsByTagName('script')[0];
  if (firstScript && firstScript.parentNode) {
    firstScript.parentNode.insertBefore(script, firstScript);
  } else {
    document.head.appendChild(script);
  }

  // Initialisation avec l'ID de pixel
  fbq('init', FB_PIXEL_ID);
  
  // Premier PageView automatique
  fbq('track', 'PageView');
  
  console.log('[Facebook Pixel] Initialis avec succs');
};

/**
 * Envoie un vnement au Facebook Pixel
 * @param eventName Nom de l'vnement  tracker
 * @param data Donnes supplmentaires pour l'vnement
 */
export const trackFacebookEvent = (eventName: string, data?: Record<string, unknown>): void => {
  // Scurit pour les environnements non-navigateur
  if (typeof window === 'undefined') {
    return;
  }
  
  // Initialisation automatique si ncessaire
  if (!isPixelLoaded()) {
    console.warn('[Facebook Pixel] Tentative de tracking sans initialisation du pixel. Initialisation automatique...');
    initFacebookPixel();
    
    // Attendre un court instant pour que le pixel soit charg
    setTimeout(() => {
      if (isPixelLoaded()) {
        window.fbq('track', eventName, data);
        console.log(`[Facebook Pixel] vnement track aprs initialisation: ${eventName}`, data);
      } else {
        console.error(`[Facebook Pixel] chec de l'initialisation du pixel pour l'vnement: ${eventName}`);
      }
    }, 500);
    return;
  }
  
  // Envoi de l'vnement
  try {
    window.fbq('track', eventName, data);
    console.log(`[Facebook Pixel] vnement track: ${eventName}`, data);
  } catch (error) {
    console.error(`[Facebook Pixel] Erreur lors du tracking de l'vnement ${eventName}:`, error);
  }
};

/**
 * Vrifie l'tat du pixel Facebook
 * @returns tat du pixel Facebook
 */
export const checkPixelStatus = (): { loaded: boolean; initialized: boolean; details: string } => {
  const status = {
    loaded: false,
    initialized: false,
    details: ''
  };
  
  if (typeof window === 'undefined') {
    status.details = 'Environnement non-navigateur';
    return status;
  }
  
  status.loaded = typeof window.fbq === 'function';
  
  if (!status.loaded) {
    status.details = 'Le pixel Facebook n\'est pas charg (window.fbq n\'est pas une fonction)';
    return status;
  }
  
  // Vrifier si le pixel est initialis avec notre ID
  if (window.fbq.queue) {
    try {
      const queue = window.fbq.queue as unknown[][];
      status.initialized = queue.some(item => 
        Array.isArray(item) && 
        item[0] === 'init' && 
        item[1] === FB_PIXEL_ID
      );
      
      if (status.initialized) {
        status.details = `Pixel Facebook charg et initialis avec l'ID ${FB_PIXEL_ID}`;
      } else {
        status.details = `Pixel Facebook charg mais non initialis avec l'ID ${FB_PIXEL_ID}`;
      }
    } catch (error) {
      status.details = `Erreur lors de la vrification de l'initialisation: ${error}`;
    }
  } else {
    status.details = 'Pixel Facebook charg mais la queue n\'est pas disponible';
  }
  
  return status;
}

/**
 * Fonction de dbogage pour le pixel Facebook
 * Affiche des informations dtailles dans la console
 */
export const debugFacebookPixel = (): void => {
  console.group('%c Facebook Pixel Dbogage', 'color: #4267B2; font-weight: bold; font-size: 14px;');
  
  const status = checkPixelStatus();
  console.log(`%ctat: ${status.loaded ? ' Charg' : ' Non charg'}`, 'font-weight: bold');
  console.log(`%cInitialis: ${status.initialized ? ' Oui' : ' Non'}`, 'font-weight: bold');
  console.log(`%cDtails: ${status.details}`, 'color: #666');
  
  if (typeof window !== 'undefined') {
    console.log('%cObjet window.fbq:', 'font-weight: bold');
    console.dir(window.fbq);
    
    if (window.fbq && window.fbq.queue) {
      console.log('%cContenu de la queue:', 'font-weight: bold');
      console.table(window.fbq.queue);
    }
    
    console.log('%cScripts Facebook chargs:', 'font-weight: bold');
    const scripts = document.querySelectorAll('script[src*="facebook"]');
    if (scripts.length > 0) {
      scripts.forEach(script => {
        // Cast vers HTMLScriptElement pour accder  la proprit src
        const scriptElement = script as HTMLScriptElement;
        console.log(scriptElement.src);
      });
    } else {
      console.log('Aucun script Facebook trouv dans le DOM');
    }
  }
  
  console.groupEnd();
}
