import { StrictMode } from 'react';
import { createRoot } from 'react-dom/client';
import { BrowserRouter } from 'react-router-dom';
import App from './App.tsx';
import './index.css';
import { initFacebookPixel } from './utils/facebookPixel';
import { initTikTokPixel } from './utils/tiktokPixel';
import { setupAutoTracking, addTrackingToSpecificButtons } from './utils/autoTrackDownloads';

// Initialiser les pixels avant le rendu de l'application
initFacebookPixel();
initTikTokPixel();

// Configurer le tracking automatique des liens de tlchargement
setupAutoTracking();

// Ajouter manuellement le tracking aux boutons spcifiques
// Attendre que le DOM soit compltement charg
if (document.readyState === 'loading') {
  document.addEventListener('DOMContentLoaded', addTrackingToSpecificButtons);
} else {
  setTimeout(addTrackingToSpecificButtons, 500); // Attendre un peu pour s'assurer que tout est charg
}

createRoot(document.getElementById('root')!).render(
  <StrictMode>
    <BrowserRouter>
      <App />
    </BrowserRouter>
  </StrictMode>
);
